import cx from 'clsx';
import React, { FC, MouseEvent, useState } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigLancamentoEntradaSaida } from '../../../../models/dtos/mlf/configLancamentoEntradaSaida';
import { TIPO_EMITENTE_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoEmitenteEnum';
import { ENTRADA, SAIDA } from '../../../../models/enumerated/mlf/tipoMovimentoEnum';
import { TIPOS_PAGAMENTO_A_VISTA_A_PRAZO_SEM_PAGAMENTO_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoPagamentoEnum';
import CFOPIS from '../../../components/ger/cfopIS/CFOPIS';
import EspecieIS from '../../../components/ger/especieIS/EspecieIS';
import SerieIS from '../../../components/ger/serieIS/SerieIS';

interface ConfigLancamentoEntradaSaidaProps extends EditViewProps<ConfigLancamentoEntradaSaida> {}

const ConfigLancamentoEntradaSaidaEditView: FC<ConfigLancamentoEntradaSaidaProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const [activeTab, setActiveTab] = useState(1);
  const formProps = useForm<ConfigLancamentoEntradaSaida>({ defaultValues: selectedItem, mode: 'onBlur' });

  const toggleTab = ({ target }: MouseEvent) => {
    const { tabIndex } = target as HTMLElement;
    if (activeTab !== tabIndex) {
      setActiveTab(tabIndex);
    }
  };

  return (
    <WindowComponent title="Manutenção de Configuração de Lançamento de Entrada e Saída">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 120, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
            />
          </Col>
        </Row>
        <Nav tabs={true}>
          <NavItem>
            <NavLink className={cx({ active: activeTab === 1 })} tabIndex={1} onClick={toggleTab}>
              Entrada
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink className={cx({ active: activeTab === 2 })} tabIndex={2} onClick={toggleTab}>
              Saída
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent className="mb-5" activeTab={activeTab}>
          <TabPane tabId={1} className="mb-0 p-3">
            <Row>
              <Col>
                <InputGroupInline
                  id="tipoEmitenteEntradaInt"
                  label="Tipo de Emitente "
                  formProps={formProps}
                  isPrependLabel={true}
                  placeholder="Informe o tipo de emitente"
                  inputDropDownProps={{ optionsProvider: TIPO_EMITENTE_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="tipoPagamentoEntradaInt"
                  label="Tipo de Pagamento"
                  formProps={formProps}
                  isPrependLabel={true}
                  placeholder="Informe o tipo de pagamento"
                  inputDropDownProps={{ optionsProvider: TIPOS_PAGAMENTO_A_VISTA_A_PRAZO_SEM_PAGAMENTO_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <EspecieIS id="especieEntrada" formProps={formProps} />
              </Col>
            </Row>
            <Row>
              <Col>
                <SerieIS id="serieEntrada" formProps={formProps} label="Série" placeholder="Informe a Série" />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopProdutoNoEstadoEntrada"
                  tipoMovimento={ENTRADA.cfops}
                  label="CFOP de produto no estado"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopProdutoOutroEstadoEntrada"
                  tipoMovimento={ENTRADA.cfops}
                  label="CFOP de produto em outro estado "
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopProdutoExteriorEntrada"
                  tipoMovimento={ENTRADA.cfops}
                  label="CFOP de produto no exterior"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopServicoNoEstadoEntrada"
                  tipoMovimento={ENTRADA.cfops}
                  label="CFOP de serviço no estado"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopServicoOutroEstadoEntrada"
                  tipoMovimento={ENTRADA.cfops}
                  label="CFOP de serviço em outro estado"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopServicoExteriorEntrada"
                  tipoMovimento={ENTRADA.cfops}
                  label="CFOP de serviço no exterior"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  id="fixarUltimoFornecedorDigitadoEntrada"
                  name="fixarUltimoFornecedorDigitadoEntrada"
                  label="Fixar último fornecedor digitado"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  id="fixarUltimaCfopDigitadaEntrada"
                  name="fixarUltimaCfopDigitadaEntrada"
                  label="Fixar última CFOP digitada"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  id="abaterImpostosRetidosContaPagar"
                  name="abaterImpostosRetidosContaPagar"
                  label="Abater do contas a pagar o valor de imposto retidos"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId={2} className="mb-0 p-3">
            <Row>
              <Col>
                <InputGroupInline
                  id="tipoPagamentoSaidaInt"
                  label="Tipo de Pagamento"
                  formProps={formProps}
                  isPrependLabel={true}
                  placeholder="Informe o tipo de pagamento"
                  inputDropDownProps={{ optionsProvider: TIPOS_PAGAMENTO_A_VISTA_A_PRAZO_SEM_PAGAMENTO_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <EspecieIS id="especieSaida" formProps={formProps} />
              </Col>
            </Row>
            <Row>
              <Col>
                <SerieIS id="serieSaida" formProps={formProps} label="Série" placeholder="Informe a Série" />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopProdutoNoEstadoSaida"
                  tipoMovimento={SAIDA.cfops}
                  label="CFOP de produto no estado"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopProdutoOutroEstadoSaida"
                  tipoMovimento={SAIDA.cfops}
                  label="CFOP de produto em outro estado "
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopProdutoExteriorSaida"
                  tipoMovimento={SAIDA.cfops}
                  label="CFOP de produto no exterior"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopServicoNoEstadoSaida"
                  tipoMovimento={SAIDA.cfops}
                  label="CFOP de serviço no estado"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopServicoOutroEstadoSaida"
                  tipoMovimento={SAIDA.cfops}
                  label="CFOP de serviço em outro estado"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CFOPIS
                  formProps={formProps}
                  id="cfopServicoExteriorSaida"
                  tipoMovimento={SAIDA.cfops}
                  label="CFOP de serviço no exterior"
                  placeholder="Informe um ou vários CFOP's"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  id="sequenciarNumeroNotaSaida"
                  name="sequenciarNumeroNotaSaida"
                  label="Sequencia número da nota"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  id="sequenciarNumeroNotaSaidaEspecie"
                  name="sequenciarNumeroNotaSaidaEspecie"
                  label="Sequenciar por espécie"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  id="fixarUltimaCfopDigitadaSaida"
                  name="fixarUltimaCfopDigitadaSaida"
                  label="Fixar última CFOP digitada"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  id="abaterImpostosRetidosContaReceber"
                  name="abaterImpostosRetidosContaReceber"
                  label="Abater do contas a pagar o valor de imposto retidos"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigLancamentoEntradaSaidaEditView;
