import React, { FC } from 'react';
import { ComparatorEnum, InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { InputSearchProps } from 'summer/dist/views/components/inputSearch/InputSearch';
import { CLASS_NAME } from '../../../../models/dtos/ger/cfop';

interface CfopIsProps extends InputProps {
  tipoMovimento: any[];
}

const CFOPIS: FC<CfopIsProps> = props => {
  function searchExpressions(): SearchExpression[] {
    const expressions: SearchExpression[] = [];

    if (props.tipoMovimento) {
      for (const tipo of props.tipoMovimento) {
        const alias = `c${tipo}`;
        const fieldName = 'codigo';
        expressions.push({
          alias,
          comparator: ComparatorEnum.LIKE,
          conditionalGroup: 'c_',
          fieldName,
          operation: `${fieldName} ${ComparatorEnum.LIKE} :${alias}`,
          ordered: false,
          value: `${tipo}%`,
        });
      }
    }

    return expressions;
  }

  const customInputSearchProps: InputSearchProps<any> = {
    ...props.inputSearchProps,
    columns: [
      {
        dataField: 'codigo',
        text: 'Código',
      },
      {
        dataField: 'descricao',
        text: 'Descrição',
      },
    ],
    dtoClassName: CLASS_NAME,
    searchExpressions,
    searchViewId: 'CFOPSearchView',
  };

  return (
    <InputGroupInline
      {...props}
      inputSearchProps={customInputSearchProps}
      isPrependLabel={true}
      label={props.label ? props.label : 'CFOP'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o CFOP'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default CFOPIS;
